                </h3>
								<div class="zm" 
                  v-if="legal_card_photofir === ''">
									<img src="../../img/larg_card.png" alt="身份证正面" />
								</div>
								<div class="zm" v-else>
									<div class="bg"></div>
									<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+legal_card_photofir" alt="身份证正面" />
									<big-img v-if="showImg" 
