								</div>
								<span class="btn" v-if='IDcodeshow'>上传</span>
								<img-upload v-if='!IDcodeshow' 
                  :FreedomCrop="true"  
                  @on-change="showButton" 
                  @send-image='getfront' 
                  :ImageSize='imgSize'></img-upload>
							</div>
							<div class="frontFace left">
								<h3 class=" titles">反面 
                  <span class="confmd" v-show='showFm' 
                  :class="[{gr:Iautionoks},{reds:Iautionerrs},{or:Iinautions}]">({{idcert}})</span>
                </h3>
								<div class="fm" 
                  v-if="legal_card_photosec === ''">
									<img src="../../img/larg_card2.png" alt="身份证反面" />
								</div>
								<div class="fm" v-else>
									<div class="bg"></div>
									<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+legal_card_photosec" alt="身份证反面" />
									<big-img v-if="showimg"   @clickit="viewimg" :imgSrc="Imgsrc"></big-img>
									<i class="el-icon-view" @click='clickimg'></i>
								</div>
								<span class="btn" v-if='IDcodeshow'>上传</span>
								<img-upload v-if='!IDcodeshow' 
                  :FreedomCrop="true" 
                  @send-image='getfm' 
                  @on-change="showButton" 
                  :ImageSize='imgSize'></img-upload>
							</div>
							<div class="text-warp left">
								<p class="text">注：支持JPG、JPEG、PNG文件格式，每张大小不得大于2M</p>
							</div>
						</div>
					</el-form-item>

          <!-- 营业执照注册号 -->
					<el-form-item class='legar-card' 
            label="营业执照注册号">
						<el-input @change="clearBusnum" 
              @blur="confimbus" 
              v-model='forms.business_num' 
              v-show='!BScodeshow' 
              :class="{errtip: isShowErrtip}"></el-input>
            <span v-show='!BScodeshow' 
