            <span v-show='BScodeshow' :class="[{gr:bautionoks},{reds:bautionerrs},{or:binautions}]">({{bscert}})</span>
					</el-form-item>

          <!-- 营业期限 -->
					<el-form-item class='dates star' label="营业期限">
						<span class="start" 
              v-show='!BScodeshow'></span>
            <i v-show='!BScodeshow' class="starts">从</i>
						<el-date-picker v-show='!BScodeshow'
              class='ipt' 
              type="date" 
              v-model='date1' 
              @change="bustimeStartChange"></el-date-picker>
						<span class="start1" v-show='!BScodeshow'></span>
            <i v-show='!BScodeshow' class="starts2">至</i>
						<el-date-picker v-show='!BScodeshow' 
              class='ipt' 
              type="date" 
              v-model='date2' 
              @change="bustimeEndChange"></el-date-picker>
						<span v-show='showBustime' 
              :class="[{gr:bautionoks},{reds:bautionerrs},{or:binautions}]">({{bscert}})</span>
						<span v-show='BScodeshow' 
              class="domain" 
              v-for='(item,index) in dat'>{{item}}
              <i v-if="index < dat.length - 1">&nbsp;&nbsp;&nbsp;&nbsp;至 </i> 
            </span> 
            <span v-show='BScodeshow' :class="[{gr:bautionoks},{reds:bautionerrs},{or:binautions}]">({{bscert}})</span>
					</el-form-item>

          <!-- 营业执照扫描件 -->
					<el-form-item  
            class='legar-card' 
            label="营业执照扫描件">
						<div class="picInfo">
							<div class="busPic left" v-if='!buspic'>
								<img src="../../img/busspic.png" alt="营业执照扫描件" />
							</div>
							<div class="busPic left" v-else>
								<div class="bg"></div>
								<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+buspic" alt="营业执照扫描件" />
								<big-img v-if="showbus" @clickit="viewbus" :imgSrc="busPicsrc"></big-img>
								<i class="el-icon-view" @click='clickbus'></i>
							</div>
							<span class="btns" v-if='BScodeshow'>上传</span>
							<span v-show='showBuspic' 
                class="busp" 
                :class="[{gr:bautionoks},{reds:bautionerrs},{or:binautions}]">
                ({{bscert}})
              </span>
							<img-upload v-if='!BScodeshow' 
                :FreedomCrop="true"   
                @on-change="showButton" 
                @send-image='getBuspic' 
                :ImageSize='imgSize'></img-upload>
							<div class="text-warp">
								<p class="text top pstion">注：支持JPG、JPEG、PNG文件格式，大小不得大于2M</p>
							</div>
						</div>
					</el-form-item>
          

          <!-- 餐饮服务许可证编号 -->
					<el-form :inline="true" class='forms'>
						<el-form-item class='citys xx' label='餐饮服务许可证编号'>
							<el-select @change="showDeb" 
                v-model="value" 
                placeholder="省份(简称)" 
                v-show='!RScodeshow'>
								<el-option v-for="item in options" 
                  :key="item.value"
                  :label="item.label" 
                  :value="item.value">
								</el-option>
							</el-select>
							<span class="domain" v-show='RScodeshow'>
                {{shopsbusiness.repast_permit_identity}}
                餐证字({{shopsbusiness.repast_permit_year}}) 
                第{{shopsbusiness.repast_permit_num}}号 
                <span v-show='RScodeshow' 
                  :class="[{gr:rautionoks},{reds:rautionerrs},{or:rinautions}]">
                  ({{rscert}})
                </span>
              </span>
						</el-form-item>

						<el-form-item class='citys cit' label='餐证字' v-show='!RScodeshow'>
							<el-date-picker @change="showDeb" 
                v-model="value5" 
                align="right" 
                type="year" 
                placeholder="发证年份">
							</el-date-picker>
						</el-form-item>

						<el-form-item class='cityd' 
              label='第' 
              v-show='!RScodeshow'>
							<el-input @change="clearResnum" 
                @blur="confimres" 
                placeholder='输入行政区域代码及发证顺序编号' 
                v-model='forms.repast_permit_num' 
                :class="{errtip: isShowErrtip}"></el-input>
							<span v-show='Errresnum' class="nobind wd">请输入正确格式的信息</span>
						</el-form-item>

						<el-form-item class="num" label='号' v-show='hide'>
						</el-form-item>

						<span style="padding-left:58px;" 
              v-show='showResnum' 
              :class="[{gr:rautionoks},{reds:rautionerrs},{or:rinautions}]">({{rscert}})</span>
					</el-form>

					<el-form-item  class ='respic'label="餐饮服务许可证扫描件">
						<div class="picInfo">
							<div class="res left" v-if="!respic">
								<img src="../../img/respic.png" alt="餐饮服务许可证扫描件" />
							</div>
							<div class="res left" v-else>
								<div class="bg"></div>
								<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+respic" alt="餐饮服务许可证扫描件" />
								<big-img v-if="showres" @clickit="viewres" :imgSrc="resPicsrc"></big-img>
								<i class="el-icon-view" @click='clickres'></i>
							</div>
							<span class="btns" v-if='RScodeshow'>上传</span>
							<span v-show='showRespic'
                class="resp" 
                :class="[{gr:rautionoks},{reds:rautionerrs},{or:rinautions}]">
                ({{rscert}})
              </span>

							<img-upload v-if='!RScodeshow' 
                :FreedomCrop="true"  
                @on-change="showButton" 
                @send-image='getRespic' 
                :ImageSize='imgSize'>
              </img-upload>

							<div class="text-warp">
								<p class="text top pstion">注：支持JPG、JPEG、PNG文件格式，大小不得大于2M</p>
							</div>
						</div>
					</el-form-item>

					<el-form-item  class='stars' 
            label="确认书扫描件">
						<div class="picInfo">
							<div class="confim left" v-if="!confm">
								<img src="../../img/confim.png" alt="确认书扫描件" />
							</div>
							<div class="confim left" v-else>
								<div class="bg"></div>
								<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+confm" alt="确认书扫描件" />
								<big-img v-if="showcos" @clickit="viewcos" :imgSrc="cosPicsrc"></big-img>
								<i class="el-icon-view" @click='clickcos'></i>
							</div>
							<span class="btns" v-if='RScodeshow'>上传</span>
							<span v-show='showConfim' 
                class="resp" 
                :class="[{gr:rautionoks},{reds:rautionerrs},{or:rinautions}]">({{rscert}})</span>
							<img-upload v-if='!RScodeshow' 
                :FreedomCrop="true"  
                @on-change="showButton" 
                @send-image='getConfimpic' 
                :ImageSize='imgSize'></img-upload>
							<div class="downLoad">
								<p class="load">下载确认书模板</p>
								<p class="text">注：支持JPG、JPEG、PNG文件格式，大小不得大于2M</p>
							</div>
						</div>
					</el-form-item>

					<el-form-item class='star' label="经营范围">
						<el-input @change='showDeb' 
              v-model='forms.business_scope' 
              v-show='!RScodeshow'></el-input>
						<span class="domain" v-if='RScodeshow'>{{shopsbusiness.business_scope}}</span>
            <span v-show='showConfim' 
            class="resp" 
            :class="[{gr:rautionoks},{reds:rautionerrs},{or:rinautions}]">({{rscert}})</span>
					</el-form-item>


					<div class="bottom">
						<button type='button' 
              ref="btn" 
              :class="{blue: showBtn}" 
              class="defau" 
              @click='onSubmit'
              v-if='btnShow'>提交审核</button>
						<button type="button" class="default" @click='goBack'>关闭</button>
					</div>
				</el-form>
			</div>
		</section>
		<!--编辑工商信息 end-->
		<!--提交成功失败 Tips start-->
		<div class="tips" v-if='showTips'>
			<span class="info">{{msg}}</span>
		</div>
		<!--提交成功失败 Tips end-->
	</div>
</template>

<script>
import moment from "moment";
import ImgUpload from "@/components/upload/SUpload";
import BigImg from "../../../components/showBigImg";

import { saveShopinfo, CommerStatus } from "../../../api";
import { getShopinfo } from "../../../api";
import { Util } from "@/config/util";
import { Business, Idcode, Dining } from "@/config/cfg";
import { Confimcard, Character, Busnumber, Resnumber } from "../../../index";
import { Public } from "@/config/public";
import { PROVICEDATA } from "../../../provicedata";

export default {
  components: {
    ImgUpload,
    BigImg
  },
  data() {
    return {
      options: PROVICEDATA,
      value: "", //存选中省的简称
      date1: "", //开始日期
      date2: "", //结束日期
      value5: "", //保存选中的年份
      forms: {
        company_name: "", //企业名称
        legal_person: "", //法人代表
        legal_card: "", //法人身份证
        business_num: "", //  营业执照
        repast_permit_identity: "", //营业期限
        repast_permit_year: "", //发证年份
        repast_permit_num: "", // 证号
        business_scope: "" //经营范围
      },
      shopsbusiness: {}, //用来接收获取过来的数据
      dates: [], //保存营业期限日期
      years: "", // 发证年份
      imgbase_url: imgbase_url, //图片地址
      hide: true, //控制输入框的显示与隐藏

      //三个部分显示
      IDcodeshow: false,
      BScodeshow: false,
      RScodeshow: false,

      showBuss: true,
      showRess: true,
      showConfms: true,
      dat: [], //获取营业期限日期

      btnShow: true, //提交按钮显示

      cert: {
        bs_code: 0,
        id_code: 0,
        rs_code: 0
      }, //获取认证状态
      bscert: "", //营业部分状态
      idcert: "", //身份部分状态
      rscert: "", //餐饮部分状态
      inaution: "or", //添加class类
      autionerr: "reds", //添加class类
      autionok: "gr", //添加class类

      /*控制三大部分状态样式*/
      rinautions: false,
      rautionoks: false,
      rautionerrs: false,
      binautions: false,
      bautionoks: false,
      bautionerrs: false,
      Iinautions: false,
      Iautionoks: false,
      Iautionerrs: false,
      /*控制三大部分状态样式*/

      legal_card_photo: [], //保存身份证
      legal_card_photofir: "",
      legal_card_photosec: "",

      buspic: "", //保存营业执照图片
      respic: "", //保存餐饮许可证图片
      confm: "", //保存餐饮许可证确认书
      imgSize: 2048, //上传图片尺寸上限
      imgSrc: "", //图片地址
      /*控制大图显示*/
      showImg: false,
      showimg: false,
      showbus: false,
      showres: false,
      showcos: false,
      Imgsrc: "",
      busPicsrc: "",
      resPicsrc: "",
      cosPicsrc: "",
      /*控制大图显示*/
      msg: "", //提示框提示内容
      showTips: false, //控制显示Tips提示框
      isShowErrtip: false, //用来判断加错误的样式
      showBtn: false, //显示按钮激活的样式
      /*错误的提示信息*/
      Errperson: false,
      Errcard: false,
      Errbusnum: false,
      Errresnum: false,
      /*错误的提示信息*/
      /* 控制认证状态的样式(默认的)*/
      showCname: false,
      showPerson: false,
      showCard: false,
      showFront: false,
      showFm: false,
      showBusnum: false,
      showBustime: false,
      showBuspic: false,
      showResnum: false,
      showRespic: false,
      showConfim: false,
      /* 控制认证状态的样式(默认的)*/

      Idcode: Idcode
    };
  },
  created() {
    this.getData();
    this.getStatus();
  },
  computed: {
    Business() {
      return Business;
    },
    // Idcode() {
    //   return Idcode;
    // },
    Dining() {
      return Dining;
    }
  },
  methods: {
    //获取身份证正面图片信息
    getfront(res) {
      // this.$set(this.legal_card_photo, 0, res);
      this.legal_card_photofir = res;
      this.showFront = false;
    },
    //获取身份证反面图片信息
    getfm(res) {
      // this.$set(this.legal_card_photo, 1, res);
      this.legal_card_photosec = res;
      this.showFm = false;
    },
    //获取营业执照扫描件图片信息
    getBuspic(res) {
      this.buspic = res;
      this.showBuspic = false;
    },
    //获取餐饮扫描件图片信息
    getRespic(res) {
      this.respic = res;
      this.showRespic = false;
    },
    //获取确认书图片信息
    getConfimpic(res) {
      this.confm = res;
      this.showConfim = false;
    },
    /*点击出现大图及给图片地址赋值*/
    clickImg() {
      this.showImg = true;
      // 获取当前图片地址
      this.imgSrc = this.legal_card_photofir;
    },
    clickimg() {
      this.showimg = true;
      // 获取当前图片地址
      this.Imgsrc = this.legal_card_photosec;
    },
    clickbus() {
      this.showbus = true;
      this.busPicsrc = this.buspic;
    },
    clickres() {
      this.showres = true;
      this.resPicsrc = this.respic;
    },
    clickcos() {
      this.showcos = true;
      this.cosPicsrc = this.confm;
    },
    /*点击出现大图及给图片地址赋值*/
    /*点击关闭大图*/
    viewcos() {
      this.showcos = false;
    },
    viewImg() {
      this.showImg = false;
    },
    viewres() {
      this.showres = false;
    },
    viewbus() {
      this.showbus = false;
    },
    viewimg() {
      this.showimg = false;
    },
    /*点击关闭大图*/

    /*保存数据*/
    onSubmit() {
      let sta1 = moment(this.date1).format("X");
      let sta2 = moment(this.date2).format("X");
      this.dates.push(sta1, sta2);
      let t3 = new Date(this.value5);
      this.years = t3.getFullYear();
      let shop_id = Public.getShopid();

      let data = {
        save_shop_business: 1,
        shop_id: shop_id,
        company_name: this.forms.company_name,
        legal_person: this.forms.legal_person,
        legal_card: this.forms.legal_card,
        business_num: this.forms.business_num,
        repast_permit_identity: this.value,
        repast_permit_year: this.years,
        // legal_card_photo: this.legal_card_photo,
        business_photo: this.buspic,
        repast_permit_photo: this.respic,
        confirmation: this.confm,
        repast_permit_num: this.forms.repast_permit_num,
        business_scope: this.forms.business_scope,
        business_date: this.dates
      };

      let legal_card_photo = [];
      legal_card_photo.push(this.legal_card_photofir);
      legal_card_photo.push(this.legal_card_photosec);
      data["legal_card_photo"] = legal_card_photo;

      saveShopinfo(data, this.alts);
    },
    //保存后的回调
    alts(resp) {
      if (resp.ret === 0) {
        this.showTips = true;
        this.msg = "提交成功";
        setTimeout(() => {
          this.$router.go(-1);
        }, 1500);
      } else {
        this.showTips = true;
        this.msg = "提交失败";
        setTimeout(() => {
          this.showTips = false;
        }, 1500);
      }
    },
    /*保存数据*/
    goBack() {
      //跳转到店铺信息
      this.$router.go(-1);
    },
    /*获取数据*/
    getData() {
      // let this = this;
      getShopinfo(
        {
          get_shop_business: 1
        },
        resp => {
          if (resp.ret === 0) {
            this.shopsbusiness = resp.data.shopsbusiness || {};
            this.forms.company_name = this.shopsbusiness.company_name;
            this.forms.legal_person = this.shopsbusiness.legal_person;
            this.forms.legal_phone = this.shopsbusiness.legal_phone;
            this.forms.legal_card = this.shopsbusiness.legal_card;
            this.forms.business_num = this.shopsbusiness.business_num;
            this.forms.business_scope = this.shopsbusiness.business_scope;

            if (this.shopsbusiness.repast_permit_year) {
              this.value5 = new Date(
                String(this.shopsbusiness.repast_permit_year)
              );
            }
            this.value = this.shopsbusiness.repast_permit_identity;
            this.forms.repast_permit_num = this.shopsbusiness.repast_permit_num;

            let legal_card_photoA = this.shopsbusiness.legal_card_photo || [];
            this.legal_card_photofir = legal_card_photoA[0] || "";
            this.legal_card_photosec = legal_card_photoA[1] || "";

            let datesArr = this.shopsbusiness.business_date || [];
            this.date1 = moment(Number(datesArr[0]) * 1000).format(
              "YYYY-MM-DD"
            );
            this.date2 = moment(Number(datesArr[1]) * 1000).format(
              "YYYY-MM-DD"
            );

            if (!Util.isEmpty(this.shopsbusiness.legal_card_photo)) {
              this.showFront = true;
              this.showFm = true;
            }

            if (!Util.isEmpty(this.shopsbusiness.business_date)) {
              let time1 = this.shopsbusiness.business_date.slice(
                  0,
                  this.shopsbusiness.business_date.length - 1
                ),
                time2 = time1.join(","),
                stime = moment(time2 * 1000).format("YYYY-MM-DD");
              let time3 = this.shopsbusiness.business_date.slice(
                  1,
                  this.shopsbusiness.business_date.length
                ),
                time4 = time3.join(","),
                etime = moment(time4 * 1000).format("YYYY-MM-DD");
              this.dat.push(stime, etime);
            }

            if (!Util.isEmpty(this.shopsbusiness.business_photo)) {
              this.buspic = this.shopsbusiness.business_photo;
              this.showDefimg = false;
              this.showBuspic = true;
            }
            if (!Util.isEmpty(this.shopsbusiness.repast_permit_photo)) {
              this.respic = this.shopsbusiness.repast_permit_photo;
              this.showDefimg = false;
              this.showRespic = true;
            }
            if (!Util.isEmpty(this.shopsbusiness.confirmation)) {
              this.confm = this.shopsbusiness.confirmation;
              this.showDefimg = false;
              this.showConfim = true;
            }
          }
        }
      );
    },
    /*获取数据*/

    //获取认证状态
    getStatus() {
      // let _this = this;

      CommerStatus(
        {
          get_shop_bs_status: 1
        },
        resp => {
          if (resp.ret === 0) {
            let shop_bs_status = resp.data.shop_bs_status || {};
            this.cert.bs_code = shop_bs_status.bs_code;
            this.cert.id_code = shop_bs_status.id_code;
            this.cert.rs_code = shop_bs_status.rs_code;

            if (this.cert.bs_code === Business.AUTONERR) {
              this.bscert = Business.toString(Business.AUTONERR);
              this.bautionerrs = true;
              this.showBusnum = true;
              this.showBustime = true;
              this.showBuspic = true;
            } else if (this.cert.bs_code === Business.INAUTON) {
              this.bscert = Business.toString(Business.INAUTON);
              this.binautions = true;
              this.showOther = true;
              this.hide = false;
              this.IDcodeshow = true;
            } else if (this.cert.bs_code === Business.YES) {
              this.bscert = Business.toString(Business.YES);
              this.bautionoks = true;
              this.hide = false;
              this.IDcodeshow = true;
              this.showOther = true;
            }

            if (this.cert.id_code === Idcode.AUTONERR) {
              this.idcert = Idcode.toString(Idcode.AUTONERR);
              this.Iautionerrs = true;
              // this.showCname = true;
              // this.showPerson = true;
              // this.showCard = true;
              this.showFront = true;
              this.showFm = true;
            } else if (this.cert.id_code === Idcode.INAUTON) {
              this.idcert = Idcode.toString(Idcode.INAUTON);
              this.Iinautions = true;
              this.hide = false;
              this.IDcodeshow = true;
              this.showOther = true;
            } else if (this.cert.id_code === Idcode.YES) {
              this.idcert = Idcode.toString(Idcode.YES);
              this.Iautionoks = true;
              this.hide = false;
              this.IDcodeshow = true;
              this.showOther = true;
            }

            if (this.cert.rs_code === Dining.AUTONERR) {
              this.rscert = Dining.toString(Dining.AUTONERR);
              this.rautionerrs = true;
              this.showResnum = true;
              this.showRespic = true;
              this.showConfim = true;
            } else if (this.cert.rs_code === Dining.INAUTON) {
              this.rscert = Dining.toString(Dining.INAUTON);
              this.rinautions = true;
              this.hide = false;
              this.IDcodeshow = true;
              this.showOther = true;
            } else if (this.cert.rs_code === Dining.YES) {
              this.rscert = Dining.toString(Dining.YES);
              this.rautionoks = true;
              this.hide = false;
              this.IDcodeshow = true;
              this.showOther = true;
            }

            //营业相关显示
            if (
              this.cert.bs_code === Business.INAUTON ||
              this.cert.bs_code === Business.YES
            ) {
              this.BScodeshow = true;
            } else if (this.cert.bs_code === Business.AUTONERR) {
              this.BScodeshow = false;
            }

            //身份相关显示
            if (
              this.cert.id_code === Idcode.INAUTON ||
              this.cert.id_code === Idcode.YES
            ) {
              this.IDcodeshow = true;
            } else if (this.cert.id_code === Idcode.AUTONERR) {
              this.IDcodeshow = false;
            }

            //餐饮相关显示
            if (
              this.cert.rs_code === Dining.INAUTON ||
              this.cert.rs_code === Dining.YES
            ) {
              this.RScodeshow = true;
            } else if (this.cert.rs_code === Dining.AUTONERR) {
              this.RScodeshow = false;
            }

            this.btnShow = !(
              this.BScodeshow &&
              this.IDcodeshow &&
              this.RScodeshow
            );
          }
        }
      );
    },
    /*判断开始时间不能大于结束时间*/
    checkData(startTime, endTime) {
      if (startTime === "" || endTime === "") {
        return true;
      }

      let start = parseInt(moment(startTime).format("X"));
      let end = parseInt(moment(endTime).format("X"));

      if (start > end) {
        this.showTips = true;
        this.msg = "开始时间不能大于结束时间";
        setTimeout(() => {
          this.showTips = false;
        }, 1500);
        return false;
      } else {
        return true;
      }
    },
    bustimeStartChange() {
      this.showButton();
      let results = this.checkData(this.date1, this.date2);
      if (!results) {
        this.date1 = "";
      }
    },
    bustimeEndChange() {
      this.showButton();
      let results = this.checkData(this.date1, this.date2);
      if (!results) {
        this.date2 = "";
      }
    },
    /*判断开始时间不能大于结束时间*/

    //显示激活按钮的条件判断
    showButton() {
      if (
        !Util.isEmpty(this.forms.company_name) &&
        !Util.isEmpty(this.forms.legal_person) &&
        !Util.isEmpty(this.forms.legal_card) &&
        // this.legal_card_photo.length !== 0 &&
        !Util.isEmpty(this.forms.business_num) &&
        this.date1 !== "" &&
        this.date2 !== "" &&
        this.buspic !== "" &&
        !Util.isEmpty(this.value) &&
        this.value5 !== "" &&
        !Util.isEmpty(this.forms.repast_permit_num) &&
        this.respic !== "" &&
        this.confm !== "" &&
        !Util.isEmpty(this.forms.business_scope)
      ) {
        this.showBtn = true;
        let btn = this.$refs.btn;
        // btn.disabled = false;
      } else {
        this.showBtn = false;
        let btn = this.$refs.btn;
        // btn.disabled = true;
      }
    },
    showDeb() {
      this.showButton();
    },
    clearName() {
      this.showCname = false;
      this.showButton();
    },
    clearPerson() {
      this.showPerson = false;
      this.Errperson = false;
      this.isShowErrtip = false;
      this.showButton();
    },
    clearCard() {
      this.showCard = false;
      this.Errcard = false;
      this.isShowErrtip = false;
      this.showButton();
    },
    clearBusnum() {
      this.showBusnum = false;
      this.Errbusnum = false;
      this.isShowErrtip = false;
      this.showButton();
    },
    clearResnum() {
      this.showResnum = false;
      this.Errresnum = false;
      this.isShowErrtip = false;
      this.showButton();
    },

    /*正则匹配*/
    confirm() {
      if (!Confimcard(this.forms.legal_card)) {
        this.isShowErrtip = true;
        this.Errcard = true;
        return;
      }
    },
    confimperson() {
      if (!Character(this.forms.legal_person)) {
        this.isShowErrtip = true;
        this.Errperson = true;
        return;
      }
    },
    confimbus() {
      if (!Busnumber(this.forms.business_num)) {
        this.isShowErrtip = true;
        this.Errbusnum = true;
        return;
      }
    },
    confimres() {
      if (!Resnumber(this.forms.repast_permit_num)) {
        this.isShowErrtip = true;
        this.Errresnum = true;
        return;
      }
    }
    /*正则匹配*/
  }
};
</script>

